'use client'

import React, { useState, useEffect } from 'react'
import Logo from './Logo'
import { Button } from '@/components/ui/button'
import Link from 'next/link'

const navLinks = [
  { title: "About", href: "#about" },
  { title: "Services", href: "#services" },
  { title: "Enterprise", href: "#enterprise" },
  { title: "Technology", href: "#technology" },
  { title: "Process", href: "#process" },
  { title: "Contact", href: "#contact" },
]

const Header = () => {
  const [isOpen, setIsOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 10)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const closeMenu = () => setIsOpen(false)

  const scrollToSection = (href: string) => {
    const element = document.querySelector(href)
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' })
    }
    closeMenu()
  }

  return (
    <header className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 font-sans text-base bg-background/80 backdrop-blur-sm border-b border-border/20 shadow-sm`}>
      <div className="container mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center h-20">
          <div className="flex items-center flex-1">
            <Link href="/" aria-label="Homepage" onClick={closeMenu}>
              <Logo />
            </Link>
          </div>
          
          <nav className="hidden lg:flex items-center justify-center flex-1 space-x-8">
            <button 
              onClick={() => scrollToSection('#home')} 
              className="text-muted-foreground hover:text-primary transition-colors font-medium flex items-center gap-2" 
              aria-label="Home"
            >
              <svg className="h-5 w-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
              </svg>
            </button>
            {navLinks.map((link) => (
              <button 
                key={link.title} 
                onClick={() => scrollToSection(link.href)} 
                className="text-muted-foreground hover:text-primary transition-colors font-medium"
              >
                {link.title}
              </button>
            ))}
          </nav>

          <div className="flex items-center justify-end flex-1">
            <div className="hidden lg:block">
              <Button 
                onClick={() => scrollToSection('#contact')} 
                className="bg-primary hover:bg-primary/90 text-primary-foreground"
              >
                Get a Free Consultation
              </Button>
            </div>
            <div className="flex items-center gap-3 lg:hidden">
              <button onClick={() => setIsOpen(!isOpen)} aria-label="Toggle menu" className="p-1">
                {isOpen ? (
                  <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                ) : (
                  <svg className="h-6 w-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                  </svg>
                )}
              </button>
            </div>
          </div>
        </div>
      </div>
      {isOpen && (
        <div className="lg:hidden bg-background border-t border-border">
          <nav className="flex flex-col space-y-4 p-4">
            <button 
              onClick={() => scrollToSection('#home')} 
              className="text-muted-foreground hover:text-primary transition-colors font-medium flex items-center gap-2" 
              aria-label="Home"
            >
              <svg className="h-4 w-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
              </svg>
              Home
            </button>
            {navLinks.map((link) => (
              <button 
                key={link.title} 
                onClick={() => scrollToSection(link.href)} 
                className="text-muted-foreground hover:text-primary transition-colors font-medium text-left"
              >
                {link.title}
              </button>
            ))}
            <Button 
              onClick={() => scrollToSection('#contact')} 
              className="w-full mt-4 bg-primary hover:bg-primary/90 text-primary-foreground"
            >
              Get a Free Consultation
            </Button>
          </nav>
        </div>
      )}
    </header>
  )
}

export default Header
